.welcome-sedona {
  width: 320px;
  margin: 0 auto;
  text-align: center;
}
@media (min-width: 768px) {
  .welcome-sedona {
    width: 768px;
    margin: 0 auto;
    text-align: center;
    box-sizing: border-box;
  }
}
@media (min-width: 1280px) {
  .welcome-sedona {
    width: 1200px;
    margin: 0 auto;
    text-align: center;
    box-sizing: border-box;
  }
}
.main-container__welcome-sedona {
  margin-bottom: 32px;
}
.welcome-sedona__wrapper {
  background: url("/img/back-mobile.jpg") no-repeat;
  width: 320px;
  height: 389px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .welcome-sedona__wrapper {
    background: url("/img/back-tablet.jpg") no-repeat;
    width: 768px;
    height: 518px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
  }
}
@media (min-width: 1280px) {
  .welcome-sedona__wrapper {
    background: url("/img/back-desktop.jpg") no-repeat;
    width: 1200px;
    height: 518px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
  }
}
.welcome-sedona__tri {
  background: url("/img/intro-triangle.svg") no-repeat;
  width: 320px;
  height: 57px;
  position: absolute;
  bottom: 0;
}
@media (min-width: 768px) {
  .welcome-sedona__tri {
    background: url("/img/catalog/intro-triangle-tablet.svg") no-repeat;
    width: 768px;
    height: 57px;
    position: absolute;
    bottom: 0;
  }
}
@media (min-width: 1280px) {
  .welcome-sedona__tri {
    background: url("/img/catalog/intro-triangle-desktop.svg") no-repeat;
    width: 1200px;
    height: 57px;
    position: absolute;
    bottom: 0;
  }
}
